import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import moment from 'moment-js';

import { getUserPosts } from '../../actions';

class UserPosts extends Component {
  componentWillMount() {
    this.props.dispatch(getUserPosts(this.props.user.login.id));
  }

  showUserPosts = user =>
    (user.userPosts
      ? user.userPosts.map(item => (
        <tr key={item._id}>
          <td>
            <Link to={`/user/edit-post/${item._id}`}>{item.name}</Link>
          </td>
          <td>{item.author}</td>
          <td>{moment(item.createAt).format('MM/DD/YYYY')}</td>
        </tr>
      ))
      : null);

  render() {
    const user = this.props.user;
    return (
      <div className="user_posts">
        <h4>Your reviews:</h4>
        <table>
          <thead>
            <tr>
              <th>Name</th>
              <th>Author</th>
              <th>Date</th>
            </tr>
          </thead>
          <tbody>{this.showUserPosts(user)}</tbody>
        </table>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  user: state.user,
});

export default connect(mapStateToProps)(UserPosts);



